<template>
<!-- Modal -->
<div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true" ref="modal">
  <div class="modal-dialog modal-xl" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-primary text-white">
      <h5 class="modal-title" id="exampleModalLabel">
        <span>新增產品</span>
      </h5>
      <button type="button" class="btn-close"
      data-bs-dismiss="modal" aria-label="Close">
      </button>
    </div>

    <div class="modal-body">
      <div class="row">
        <div class="col-sm-4">
          <div class="mb-3">
            <label for="image" class="form-label">輸入圖片網址</label>
            <input type="text" class="form-control" id="image"
            placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
          </div>

          <div class="mb-3">
            <label for="customFile" class="form-label">或 上傳圖片
              <i class="fas fa-spinner fa-spin"></i>
            </label>
            <input type="file" id="customFile" class="form-control"
            ref="fileInput" @change="uploadFile">
            <!-- <input type="file" id="customFile2" class="form-control"
            ref="fileInput2" @change="uploadFile2"> -->
          </div>
          <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
          
          <!-- 延伸技巧，多圖 -->
          <!-- <div class="mt-5" v-if="tempProduct.imagesUrl">
            <div v-for="(item,key) in tempProduct.imagesUrl" class="mb-3 input-group" :key="item">
              <input type="url" class="form-control form-control"
              placeholder="請輸入連結" v-model="tempProduct.imagesUrl[key]">
              <button type="button" class="btn btn-outline-danger" @click="delImage">
                移除
              </button>
            </div> -->
            <div>
              <button class="btn btn-outline-primary btn-sm d-block w-100">
                新增圖片
              </button>
            </div>
          </div>
        <!-- </div> -->

        <div class="col-sm-8">
          <div class="mb-3">
            <label for="title" class="form-label">標題</label>
            <input type="text" class="form-control" id="title"
            placeholder="請輸入標題" v-model="tempProduct.title">
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="category" class="form-label">分類</label>
              <input type="text" class="form-control" id="category"
              placeholder="請輸入分類" v-model="tempProduct.category">
            </div>

            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">單位</label>
              <input type="text" class="form-control" id="unit"
              placeholder="請輸入單位" v-model="tempProduct.unit">
            </div>
          </div>

          <div class="row gx-2">
            <div class="mb-3 col-md-6">
              <label for="origin_price" class="form-label">原價</label>
              <input type="number" class="form-control" id="origin_price"
              placeholder="請輸入原價" v-model="tempProduct.origin_price">
            </div>

            <div class="mb-3 col-md-6">
              <label for="price" class="form-label">售價</label>
              <input type="number" class="form-control" id="price"
              placeholder="請輸入售價" v-model="tempProduct.price">
            </div>
          </div>
          <hr>

          <div class="mb-3">
            <label for="description" class="form-label">產品描述</label>
            <textarea type="text" class="form-control" id="description"
            placeholder="請輸入產品描述" v-model="tempProduct.description">
          </textarea>
          </div>

          <div class="mb-3">
            <label for="content" class="form-label">說明內容</label>
            <textarea type="text" class="form-control" id="content"
            placeholder="請輸入產品說明內容" v-model="tempProduct.content">
          </textarea>
          </div>

          <div class="mb-3">
            <div class="form-check">
              <input class="form-check-input" type="checkbox"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled">
              <label class="form-check-label" for="is_enabled">
                是否啟用
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="modal-footer">
      <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
        取消更新
      </button>
      <button type="button" class="btn btn-primary"
      @click="$emit('update-product',tempProduct)">
        我要更新
      </button>
    </div>
  </div>
</div>
</div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
    props: {
      product: {
        // 預設傳進來的資料型別是物件
        type: Object,

        // 假設外層沒有正確傳遞時，給予預設值
        default() {
          return {};
        }
      }
    },

    // 監聽product內容有無變動
    // 因為單向數據流，不可直接修改外層資料
    // 所以把傳送進來的資料儲存在tempProduct(撰寫、修改資料所使用的名稱)裡
    watch: {
      product() {
        // 將外層資料傳入
        this.tempProduct = this.product;

      //   // 延伸技巧，多圖
      //   if(!this.tempProduct.imagesUrl) {
      //   this.tempProduct.imagesUrl = [];
      // }
      }
    },

    data() {
        return {
            modal: {},
            tempProduct: {}
        }
    },

    methods: {
        uploadFile() {
          // 將上傳的檔案取出
          const uploadedFile = this.$refs.fileInput.files[0];

          // 建立FormData格式內容
          const formData = new FormData();

          // 增加欄位至表單中
          formData.append('file-to-upload',uploadedFile);

          // API路徑
          const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;

          // 用form表單格式來發送內容
          this.$http.post(url,formData).then((response) => {
          if (response.data.success) {
          // 將遠端路徑儲存至tempProduct中
          this.tempProduct.imageUrl = response.data.imageUrl;
          console.log(this.tempProduct.imageUrl);
        }
      });
      },

      // uploadFile2() {
      //     // 將上傳的檔案取出
      //     const uploadedFile2 = this.$refs.fileInput2.files[0];

      //     // 建立FormData格式內容
      //     const formData = new FormData();

      //     // 增加欄位至表單中
      //     formData.append('file-to-upload',uploadedFile2);

      //     // API路徑
      //     const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;

      //     // 用form表單格式來發送內容
      //     this.$http.post(url,formData).then((response) => {
      //     if (response.data.success) {
      //     // 將遠端路徑儲存至tempProduct中
      //     this.tempProduct.imageUrl = response.data.imageUrl;
      //     console.log(this.tempProduct.imageUrl);
      //   }
      // });
      // },

      // // 移除圖片
      // delImage() {
      //   this.tempProduct.imagesUrl.splice(key,1);
      // },

      showModal() {
            // 用show()這個方法將modal呈現在畫面上
            this.modal.show();
        },
        
        hideModal() {
            // 用hide()這個方法將modal隱藏在畫面上
            this.modal.hide();
        }
    },

    mounted() {
        // 將Modal實體化，實體會指向外層的modal
        this.modal = new Modal(this.$refs.modal);       
    }
};
</script>